import React from 'react';
import {connect} from 'react-redux';

import {setPage} from "../../store/router/actions";
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import {restoreScrollPosition} from "../../services/_functions";


import {
    Div,
    Panel,
    Group,
    CellButton,
    PanelHeader,
    FixedLayout,
    HorizontalScroll,
    TabsItem,
    Tabs,
    Gallery
} from "@vkontakte/vkui";

class HomePanelProfile extends React.Component {
    state = {
      result:"",
      maxCount:"",
    };

    constructor(props) {
        super(props);

        this.state = {
            activeTab: props.activeTab["EXAMPLE"] || "modal"

        };
    }

    setTab(tab) {
        this.setState({
            activeTab: tab
        });
    }

    componentWillUnmount() {
        const {setScrollPositionByID, setActiveTab} = this.props;

        setActiveTab("EXAMPLE", this.state.activeTab);
        setScrollPositionByID("EXAMPLE_TABS_LIST");
    }

    async componentDidMount() {
        restoreScrollPosition();

          let result = await fetch('https://demo146.bravo.vkhackathon.com/api/v1/challangeContainer/getSportChallenges', {
          method: "GET",
          // body: JSON.stringify(''),
        })
        result=await result.json();

        this.state.maxCount = result.challenge.length;
        this.state.result = result.challenge;
        console.log(this.state.result[1]);
        console.log(this.state.maxCount);
        this.forceUpdate();
    }


    render() {
        const {id, setPage} = this.props;
        const boxStyle = {marginTop: 56};

        const items = [];
        for(let i = 0;i < this.state.maxCount;i++)
        items.push(<Group key={i}>
          <Gallery
            slideWidth="90%"
            align="center"
            style={{ height: 150 }}
          >
            <div>
              <p className="name">{this.state.result[i].name}</p>
              <p className="description">{this.state.result[i].body}</p>
            </div>
          </Gallery>
        </Group>);

        return (
            <Panel id={id}>
                <PanelHeader noShadow={true}>Задания</PanelHeader>
                <FixedLayout vertical="top">
                    <Tabs theme="header" type="buttons">
                        <HorizontalScroll id="EXAMPLE_TABS_LIST">
                            <TabsItem
                                onClick={() => this.setTab('modal')}
                                selected={this.state.activeTab === 'modal'}
                            >
                                Все
                            </TabsItem>
                            <TabsItem
                                onClick={() => this.setTab('test')}
                                selected={this.state.activeTab === 'test'}
                            >
                                Актуальные
                            </TabsItem>
                            <TabsItem
                                onClick={() => this.setTab('test2')}
                                selected={this.state.activeTab === 'test2'}
                            >
                                Спортивные
                            </TabsItem>
                            <TabsItem
                                onClick={() => this.setTab('test3')}
                                selected={this.state.activeTab === 'test3'}
                            >
                                Личные
                            </TabsItem>
                        </HorizontalScroll>
                    </Tabs>
                </FixedLayout>
                {items}
            </Panel>
        );
    }

}

const mapStateToProps = (state) => {
    return {
        activeTab: state.vkui.activeTab,
    };
};

const mapDispatchToProps = {
    setPage,
    setActiveTab,
    setScrollPositionByID
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePanelProfile);
